import React from "react"

export const List = ({
    variant,items
})=>{

    const ListComponent = variant ==='ordered'?'ol':'li';

    console.log(items)

    return (
        <div>
            <ul className="list-disc pl-4">
                {items.map((items,index)=>{
                    <li className="font-bold text-black" key={index}>{items}</li>
                })}
            </ul>     
        </div>
    )
}